'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { handleLoginRedirect } from '@/app/actions';
import { LOGIN_PROVIDERS } from '@/config';
import { useToast } from '@/hooks/use-toast';
import { Loader2 } from 'lucide-react'; // Using lucide-react's loader

// Dynamically import specific icons if available or use a generic one
// For simplicity, using generic login icon or text
// import { FaQq, FaWeixin, FaAlipay } from 'react-icons/fa'; // Example if using react-icons

// A generic icon component, or you can map specific ones
const ProviderIcon = ({ type }: { type: string }) => {
  // This is a placeholder. Ideally, you'd have specific icons.
  // For Shadcn/Lucide, there aren't direct brand icons for QQ, WeChat etc.
  // You might need to use SVGs or a different icon library for those.
  if (type === 'qq') return <span className="mr-2">QQ</span>;
  if (type === 'wx') return <span className="mr-2">微信</span>;
  if (type === 'alipay') return <span className="mr-2">支付宝</span>;
  return <span className="mr-2">{type.toUpperCase()}</span>;
};


export default function LoginButtons() {
  const [loadingProvider, setLoadingProvider] = useState<string | null>(null);
  const { toast } = useToast();

  const onLoginClick = async (providerType: string) => {
    setLoadingProvider(providerType);
    try {
      const result = await handleLoginRedirect(providerType);
      if (result?.error) {
        toast({
          title: '登录错误',
          description: result.error,
          variant: 'destructive',
        });
      }
      // If successful, handleLoginRedirect will perform a redirect, so no further action needed here.
    } catch (error) {
      toast({
        title: '登录失败',
        description: '发生意外错误，请稍后再试。',
        variant: 'destructive',
      });
      console.error('Login redirect error:', error);
    } finally {
      setLoadingProvider(null);
    }
  };

  return (
    <div className="space-y-4">
      {LOGIN_PROVIDERS.map((provider) => (
        <Button
          key={provider.type}
          onClick={() => onLoginClick(provider.type)}
          className="w-full justify-center py-3 text-base"
          variant="outline"
          disabled={loadingProvider === provider.type}
        >
          {loadingProvider === provider.type ? (
            <Loader2 className="mr-2 h-5 w-5 animate-spin" />
          ) : (
            <ProviderIcon type={provider.type} />
          )}
          使用 {provider.name} 登录
        </Button>
      ))}
    </div>
  );
}
